.SkillsSection {
}

.skills-container {
    .skill-title {
        font-size: 1.4rem;
        font-weight: 400;
    }

    .skill-bar {
        display: flex;
        align-items: center;
        .skill-text {
        }
        .skill-progress {
            width: 100%;
            .progress {
                width: 100%;
                height: 0.5rem;
                background-color: red;
                margin-left: 1rem;
                background-color: #0381ff3f;
                position: relative;
                .inner-pregress {
                    position: absolute;
                    background-color: #037fff;
                    height: 100%;
                    bottom: 0;
                    left: 0;
                }
                @media screen and (max-width: 1024px) {
                    width: 50%;
                }
            }
        }
    }
}
